import React from 'react'

function SignUpBasic(data) {
    return (
        <div className="account">
            <div className="account__form">
                <form onSubmit={data.onSubmit} method="post">
                    <div className="box">
                        <div className="account__head">
                        <h2></h2>
                        </div>
                        <div className="account__field">
                        <label htmlFor="username" className="hidden"></label>
                        <div className="form__input">
                            <input
                            type="email"
                            id="username"
                            placeholder=" "
                            value={data.id} 
                            required 
                            onChange={data.onChangeId}
                            onBlur={data.onfocusout}
                            />
                        </div>
                        </div>
                        <div className="account__field">
                        <label htmlFor="username" className="hidden"></label>
                        <div className="form__input">
                            <input
                            type="text"
                            id="nickname"
                            placeholder=" "
                            value={data.nick} 
                            required 
                            onChange={data.onChangeNick}
                            />
                        </div>
                        </div>
                        <div className="account__field">
                        <label htmlFor="username" className="hidden"></label>
                        <div className="form__input">
                            <input
                            type="password"
                            id="password"
                            placeholder=" "
                            value={data.password} 
                            required 
                            onChange={data.onChangePassword}
                            />
                        </div>
                        </div>
                        <div className="account__field">
                            <label htmlFor="username" className="hidden"></label>
                            <div className="form__input">
                                <input
                                type="password"
                                id="passwordcheck"
                                placeholder=" "
                                value={data.passwordCheck} 
                                required 
                                onChange={data.onChangePasswordChk}
                                />
                            </div>
                        </div>
                        
                        <div className="account__button">
                            <button
                            type="submit"
                            className="btn btn__block btn__gradient--primary"
                            >
                            <strong></strong>
                            </button>
                        </div>
                        <div className="account__field">
                            <div className="form__text">
                                <label htmlFor="username" className="hidden"> SPARKER ,       .</label>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default SignUpBasic
